.exercise-wrapper {
  margin: 0px 30px;
  padding-top: 40px;
  background-color: whitesmoke;
  padding-bottom: 50px;
}
.exercise-wrapper p {
  /* margin-left: 50px; */
}
.exe-img {
  width: 100%;
  border-radius: 5px;
  height: 565px;
}
.e-d1 {
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
  align-items: center;
}
.image-container {
  position: relative;
  /* margin-left: 50px; */
}
.exercise-detail {
  max-width: 100%;
  margin: 0 auto;
  border-bottom:  1px solid lightgray;
}

.exercis-overview {
  max-width: 88%;
}

.exercis-overview div {
  max-width: 100%;
  /* margin-left: 50px; */
  font-weight: 500;
}
.exercis-overview p {
  margin-top: 20px;
}
.play-btn {
  position: absolute;
  cursor: pointer;
  top: 50%;
  left: 50%;
  margin: -25px 0 0 -25px;
}
.fa-check {
  margin-right: 10px;
  /* color: #ec9d33; */
}
/* .fa-checked {
  color: black !important ;
} */
.check-btn {
  width: 180px;
  border: 1px solid #ec9d33;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #ec9d33;
  border-radius: 10px;
  font-size: 15px;
  font-weight: 500;
}
.press-btn {
  background-color: #ec9d33 !important;
  color: black !important;
  border-radius: 10px;
  font-size: 15px;
  width: 180px;
  border: 1px solid #ec9d33;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: default;
  font-weight: 500;
}

.check-btn-fill {
  width: 150px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 10px;
  font-size: 15px;
  font-weight: 500;
  margin-left: 0.5rem;
}

.orange-btn{
  border: 1px solid #ec9d33;
  color: #ec9d33;
}
.orange-checked{
  background: #ec9d33;
  color: #fff;
}
.green-btn{
  border: 1px solid #21D803;
  color: #21D803;
}
.green-checked{
  background: #21D803;
  color: #fff;
}
.red-btn{
  border: 1px solid #FC2020;
  color: #FC2020;
}
.red-checked{
  background: #FC2020;
  color: white;
}

.player-wrapper {
  width: auto !important;
  height: 565px !important;
}

/* ----responsivenes----- */

@media (max-width: 992px) {
  .exercise-detail {
    max-width: 100%;
  }
}

@media (max-width: 768px) {
  .menue-list {
    width: 170px;
    height: 50px;
  }
  .m1 div {
    margin-left: 16px;
  }
  .fa-home {
    color: white;
    font-size: 19px;
  }
  .exercise-detail {
    max-width: 100%;
    flex-wrap: wrap;
  }
  .exe-img {
    height: auto;
  }
  .player-wrapper {
    height: auto !important;
  }
}

@media (max-width: 570px) {
  .p2 button {
    width: 100px;
  }
  .image-container {
    margin-left: 0px;
  }
}
@media (max-width: 470px) {
  .menue-list {
    width: 180px;
    height: 50px;
  }
  .m1 div {
    margin-left: 10px;
    font-size: 13px;
  }
  .fa-home {
    color: white;
    font-size: 15px;
  }
  .p2 button {
    width: 75px;
    font-size: 13px;
  }
  .p2 button {
    height: 35px;
  }
  .check-btn {
    font-size: 12px;
  }
  .e-d1 {
    flex-wrap: wrap;
  }
  .exercis-overview {
    margin-top: 30px;
  }
  .menue-list div img {
    width: 16px;
  }
  .all_item2_1 {
    margin-top: 30px;
  }
  .exercise-detail {
    max-width: 100%;
    margin: 0 auto;
  }
  .exercis-overview div {
    margin-left: 0px;
  }
  .exercise-wrapper p {
    margin-left: 0px;
  }
}
@media (max-width: 400px) {
  .e-d1 {
    margin-left: 0px;
  }

  .image-container {
    position: relative;
    margin-left: 0px;
  }
}
