.professional_wrapper {
  margin: 0 auto;
  padding: 40px;
  background-color: whitesmoke;
}
.professional_wrapper p {
  font-size: 28px;
  color: #2e3034;
}

.main_card {
  background: #ffffff;
  border-radius: 8px;
}
.search_main {
  padding: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-right: 1px solid #e5e5e5;
}

.search_main i {
  color: #717273;
}

hr.client_hr {
  color: #717273;
  margin: 0rem;
}
.client_search {
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #b6b6b6;
  border: 0px;
}

.client_button {
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #284eda;
  width: 140px;
  height: 36px;
  border: 1px solid #284eda;
  box-sizing: border-box;
  border-radius: 70px;
  background: white;
}

.client_list_main {
  max-height: 608px;
  overflow-x: hidden;
  overflow-y: auto;
  border-right: 1px solid #e5e5e5;
}

.client_content {
  padding: 14px;
  color: #242424;
  cursor: pointer;
}
.client_content:hover {
  background: #e5e5e5;
}

.client_profile {
  margin: 2rem 0rem;
  display: flex;
  justify-content: center;
}

.client_profile img {
  width: 140px;
  height: 140px;
  border-radius: 50%;
}

.media img {
  width: 50px;
  height: 33px;
  border-radius: 2px;
}
.media-body h6 {
  font-size: 12px;
}

.themeMute {
  color: #707070;
}